import React from "react";
import type { ZhiPlugin, RenderNodeProps } from "@ali/4ever-cangjie";
import { Container } from "../../components/container";
import { TemplateButtonConfig } from '../../type';
import { TemplateButton } from '../../mo';

export function createRenderNode(config: TemplateButtonConfig): ZhiPlugin['renderNode'] {
  return (props, controller, next) => {
    const { children, node, attributes } = props;
    if (!TemplateButton.isTemplateButton(node)) return next();
    return (
      <div {...attributes}>
        <Container
          {...props as RenderNodeProps<TemplateButton>}
          controller={controller}
          child={children}
          config={config}
        />
      </div>
    );
  };
};
